.icon-scroll {
  z-index: 999;
  cursor: pointer;
  position: fixed;
  display: block;
  width: 30px;
  height: 60px;
  margin-left: -20px;
  top: 95vh;
  margin-top: -35px;
  box-shadow: inset 0 0 0 3px #e6da35;
  border-radius: 25px;
  position: absolute;
  left: 50%; }
  .icon-scroll::before {
    content: '';
    width: 8px;
    height: 8px;
    background: #e6da35;
    margin-left: -4px;
    top: 8px;
    border-radius: 4px;
    animation-duration: 1.5s;
    animation-iteration-count: infinite;
    animation-name: scroll;
    left: 50%;
    position: absolute; }

.u-scroll {
  left: 70%; }

@keyframes scroll {
  0% {
    opacity: 1; }
  100% {
    opacity: 0;
    transform: translateY(46px); } }

